import { Chip, Stack } from '@mui/material'
import { useMediaQuery } from 'react-responsive'
import { PriceLabel, WeightLabel } from '../categorisedProducts/homemadeCard/homemadeCard.styles'
import ProductImageCard from '../productImageCard/productImageCard.component'
import { Heading, ProductsPaper, StyledStack } from './pricedCategoryProducts.styles'
import DiscountTwoToneIcon from '@mui/icons-material/DiscountTwoTone'
const PricedCategoryProducts = ({ type, products, brands }) => {
  const isMobile = useMediaQuery({ query: `(max-width: 760px)` })
  return (
    <ProductsPaper elevation={4}>
      <Heading variant="h6" align="center" color="secondary.dark">
        {type.label}
      </Heading>
      <Heading variant="h6" align="center" color="primary.main">
        {type.description}
      </Heading>
      <Stack direction="row" spacing={4} justifyContent="center" sx={{ marginTop: 0 }}>
        <Heading variant="subtitle2" align="left">
          Size: {type.size}
        </Heading>
        <Heading color="secondary">{`${type.price}`}</Heading>
      </Stack>
      {type.offers && type.offers.length > 0 && (
        <Stack
          direction="row"
          spacing={1}
          justifyContent="center"
          alignItems="center"
          sx={{ marginTop: 1, marginBottom: 1 }}
        >
          <DiscountTwoToneIcon color="secondary" sx={{ fontSize: 16 }}></DiscountTwoToneIcon>
          <Chip color="secondary" label={type.offers.join(' | ')} align="left"></Chip>
        </Stack>
      )}
      <StyledStack direction="row" useFlexGap spacing={isMobile ? 2 : 3} alignItems={'center'} justifyContent="center">
        {products.map((product, index) => (
          <ProductImageCard
            key={`festive-${index}`}
            productData={product}
            brands={brands}
            type={type}
          ></ProductImageCard>
        ))}
      </StyledStack>
    </ProductsPaper>
  )
}

export default PricedCategoryProducts
